import React, { useState, useEffect, useContext, createContext } from "react";
import Graph from "./Graph";
import Menu from "./Menu";
import { FormControlLabel, Pagination, Switch } from "@mui/material";
import { UserContext } from "../App";
import { Bar } from "react-chartjs-2";
export const GraphType = createContext(null);

function Home() {
  const [sortedData, setSortedData] = useState([]);
  const [sortedFlag, setSortedFlag] = useState(false);
  const [page, setPage] = useState(1);
  const [graphType, setGraphType] = useState(Bar);
  const [chartData, setChartData] = useState();
  const post = useContext(UserContext);
  // console.log(GraphType);
  useEffect(() => {
    sortedFlag &&
      sortedData &&
      sortedData.sort(function (a, b) {
        return a.TotalConfirmed > b.TotalConfirmed
          ? -1
          : a.TotalConfirmed === b.TotalConfirmed
          ? 0
          : 1;
      });
    !sortedFlag &&
      sortedData &&
      sortedData.sort(function (a, b) {
        return a.Country < b.Country ? -1 : a.Country === b.Country ? 0 : 1;
      });
    sortedData &&
      setChartData({
        labels: sortedData.map((cn) => cn.Country),
        datasets: [
          {
            label: "Total Deaths",
            data: sortedData.map((cn) => cn.TotalDeaths),
            backgroundColor: "rgba(0, 0, 0, 0.5)",
          },
          {
            label: "Active",
            data: sortedData.map((cn) => cn.Active),
            backgroundColor: "rgba(0, 205, 70, 0.5)",
          },
          {
            label: "Total Recovered",
            data: sortedData.map((cn) => cn.TotalRecovered),
            backgroundColor: "rgba(255, 99, 132, 0.5)",
          },
          {
            label: "Total Confirmed",
            data: sortedData.map((cn) => cn.TotalConfirmed),
            backgroundColor: "rgba(53, 162, 235, 0.5)",
          },
        ],
      });
    // console.log(sortedData);
    // console.log(post);
  }, [sortedData, sortedFlag]);

  // useEffect(() => {
  //   setSortedData(
  //     post.Countries &&
  //       post.Countries.map((cn) => ({
  //         Country: cn.Country,
  //         TotalConfirmed: cn.TotalConfirmed,
  //         TotalDeaths: cn.TotalDeaths,
  //         TotalRecovered: cn.TotalRecovered,
  //       })).splice(page * 5 - 5, 5)
  //   );
  // }, [post, page]);
  useEffect(() => {
    setSortedData(
      post.length &&
        post
          .map((cn) => ({
            Country: cn.country,
            TotalConfirmed: cn.cases,
            TotalDeaths: cn.deaths,
            TotalRecovered: cn.recovered,
            Active: cn.active,
          }))
          .splice(page * 5 - 5, 5)
    );
  }, [post, page]);

  const PageChange = (event, value) => {
    setPage(value);
  };
  const flipSorted = () => {
    console.log("flipped");
    setSortedFlag((prev) => !prev);
  };
  console.log(sortedFlag);
  return (
    <>
      <GraphType.Provider value={{ graphType, setGraphType }}>
        <>
          <div className="chart-container">
            {chartData ? <Graph chartData={chartData} /> : <></>}
          </div>
          <div className="flex">
            <Pagination
              className="pagination"
              count={(post && Math.ceil(post && post.length / 5)) || 0}
              color="primary"
              variant="outlined"
              onChange={PageChange}
            />
            <FormControlLabel
              control={<Switch onChange={flipSorted} />}
              label="Sort"
            />
          </div>
          {graphType && <Menu />}
        </>
      </GraphType.Provider>
    </>
  );
}

export default Home;
